import i18n from 'i18next'
import { initReactI18next } from 'react-i18next'

import Backend from 'i18next-http-backend'
import LanguageDetector from 'i18next-browser-languagedetector'
import { langConfig } from './locales'
import { getLocalStorageServices } from 'utils/offlineServices'
import { keyLocalStorage } from './constants'

const languageSupported = ['en', 'vi']

const langDefault = getLocalStorageServices(keyLocalStorage.language)

const i18nInit = {
  fallbackLng: 'en',
  lng: langDefault ? langDefault : 'en',
  debug: false,
  whitelist: languageSupported,
  resources: langConfig,

  interpolation: {
    escapeValue: false
  }
}

i18n
  .use(Backend)
  .use(LanguageDetector)
  .use(initReactI18next)
  .init(i18nInit)


export default i18n