import { headersAuthToken } from 'utils/auth';
import { instance } from './../../apiBase/instance';

export const userApi = {

	async activePlaftform(data) {
		return instance.post(`user/active`, data, await headersAuthToken());
	},

	async connectWallet(data) {
		return instance.post(`user/connect-wallet`, data, await headersAuthToken());
	},

	async getBalance() {
		return instance.get(`user/balance`, await headersAuthToken());
	},

};

