import React, { memo, useEffect, useState } from "react";
import { View, WrapForm } from "./styled";
import { Form, Input } from 'antd';
import { useAuthentication } from "hooks/useAuthentication";
import AuthenNavigation from 'components/Navigration/authen'
import styles from '../styles.module.css'
import { NavLink } from "react-router-dom";
import { AppRoute } from "config/router/config.router";
import { formatNumbersLess10 } from "utils/number";

const Login = () => {
    const [form] = Form.useForm();
    const { forgotPassword } = useAuthentication()
    const [timeLeft, setTimeLeft] = useState(0)
    const [step, setStep] = useState(0);
    const [data, setData] = useState(null)

    const onFinish = async () => {
        const fileds = form.getFieldsValue()
        setData({ ...fileds })
        await forgotPassword(fileds).then((res) => {
            if (res.success) {
                setStep(1)
                setTimeLeft(60)
            }
        })
    };

    const resend = async () => {
        await forgotPassword(data).then((res) => {
            if (res.success) {
                setStep(1)
                setTimeLeft(60)
            }
        })
    }

    useEffect(() => {
        if (timeLeft > 0 && step === 1) {
            setTimeout(() => {
                setTimeLeft(timeLeft - 1)
            }, 1000)
        }
        // eslint-disable-next-line
    }, [timeLeft])

    return (
        <>
            <View>
                <AuthenNavigation />
                <WrapForm>
                    {
                        step === 0
                            ? (
                                <Form
                                    name="basic"
                                    form={form}
                                    className={`${styles.smt_form}`}
                                    labelCol={{ span: 8 }}
                                    wrapperCol={{ span: 16 }}
                                    onFinish={onFinish}
                                    autoComplete="off"
                                >
                                    <p className={styles.smt_titleForm}>Forgot Password</p>
                                    <p className={styles.smt_descriptionForm}>Please enter your email.</p>
                                    <Form.Item
                                        label="Email"
                                        name="email"
                                        rules={[{ required: true, message: 'Please input your email!' }]}
                                    >
                                        <Input type="email" placeholder="example @mail.com" />
                                    </Form.Item>

                                    <Form.Item>
                                        <button style={{ marginTop: '10px' }} className={styles.smt_buttonSubmit} type="submit">
                                            Send Password Reset Email
                                        </button>
                                    </Form.Item>
                                    <Form.Item style={{ marginBottom: 0 }}>
                                        <div className={styles.smt_moreOption}>
                                            <NavLink className={styles.smt_moreOption_link} to={AppRoute.login}>Login</NavLink>
                                        </div>
                                    </Form.Item>
                                </Form>
                            ) : (
                                <Form
                                    name="basic"
                                    className={`${styles.smt_form}`}
                                    autoComplete="off"
                                >
                                    <p className={styles.smt_titleForm}>Email Reset Password</p>
                                    <p style={{ marginBottom: 0 }} className={styles.smt_descriptionForm}>Please check your email box. Confirmation link is valid for 5 minutes.</p>
                                    <p className={styles.smt_descriptionForm}>Please check your spam folder if you do not receive the email.</p>
                                    <div className={styles.resend}>
                                        <Form.Item style={{ marginBottom: 0 }}>
                                            <div className={styles.smt_moreOption}>
                                                <p>Didn't receive the code? &nbsp;</p>
                                                {
                                                    timeLeft > 0
                                                        ? <p className={styles.smt_resendCode}>Resend in {formatNumbersLess10(timeLeft)} seconds</p>
                                                        : <p onClick={resend} className={styles.smt_resendCode}>Resend</p>
                                                }
                                            </div>
                                        </Form.Item>
                                    </div>
                                </Form>
                            )
                    }
                </WrapForm>
            </View>
        </>
    )
}

export default memo(Login)