export function cutTextWithSpace(text: string, length = 150): string {
  // If the text is already shorter than the specified length, no need to cut
  if (text.length <= length) {
    return text;
  }

  // Find the nearest space character after the specified length
  const lastSpace = text.lastIndexOf(' ', length);

  // Cut the text at the space character
  const shortText = text.substring(0, lastSpace);

  return `${shortText}...`;
}

export function cutText(text: string, length = 150): string {
  // If the text is already shorter than the specified length, no need to cut
  if (text.length <= length) {
    return text;
  }
  // Cut the text at the space character
  const shortText = text.substring(0, length);

  return `${shortText}...`;
}

export const shortenString = (text?: string, chars = 15): string => {
  if (text.length < chars * 2) {
    return text
  }
  return `${text.substring(0, chars)}...${text.substring(text.length - chars)}`
}

export const shortenEmail = (email, length = 3) => {
  const atIndex = email.indexOf('@');
  const username = email.substring(0, atIndex);
  const domain = email.substring(atIndex + 1);

  const shortenedUsername = `${username.substring(0, length)}...${username.substring(username.length - length)}`;
  const shortenedEmail = `${shortenedUsername}@${domain}`;

  return shortenedEmail;
}
