import React, { memo } from "react"
import {
    Body,
    Head,
    Header,
    Item,
    Items,
    TableSelection,
    Wrapper,
    Button
} from "./styled";
import { useHookTransaction } from "views/Transaction/state";
import { inoutCheckTransaction } from "utils";
import { shortenString } from "utils/string";
import { formatDate } from "utils/time";

const Table: React.FC = () => {

    const [state] = useHookTransaction()

    return (
        <Wrapper className="width-scroll">
            <TableSelection className="no-sb">
                <Header>
                    <Head width="5%">#</Head>
                    <Head width="25%">Date Order</Head>
                    <Head width="15%">Note</Head>
                    <Head width="15%">Amount </Head>
                    <Head width="25%">Txh</Head>
                    <Head width="15%">Order Status </Head>
                </Header>
                <Body>
                    {
                        state?.data?.map((item, index) => (
                            <Items key={index}>
                                <Item width="5%">#{index + 1}</Item>
                                <Item width="25%">
                                    {formatDate(item.createdAt)}
                                </Item>
                                <Item width="15%" style={{ textTransform: 'capitalize' }}>
                                    {item.note}
                                </Item>
                                <Item width="15%" className={inoutCheckTransaction(item.note) ? 'amount' : 'amount down'}>
                                    {inoutCheckTransaction(item.note) ? '' : '-'}${item.amount}
                                </Item>
                                <Item width="25%">
                                    {shortenString(item.txh, 5)}
                                </Item>
                                <Item width="15%">
                                    <Button className="btn-actions active">
                                        Active
                                    </Button>
                                </Item>
                            </Items>
                        ))
                    }
                </Body>
            </TableSelection>
        </Wrapper>
    )
}

export default memo(Table)