
import { createStore, createHook, createContainer, createSubscriber } from 'react-sweet-state'

const StateWallet = createStore({
  initialState: {
    address: '',
    balanceBNB: 0,
    balanceBUSD: 0,
    wrongNetwork: false,
    chainId: undefined,
    showInstalled: false,
    selectedWalletInfo: null,
    showModalWallet: false,
    showModal: false,
    loadingBalance: true,
    loadBalance: true
  },
  actions: {
    setShowModalWallet: (show: boolean) => ({ setState }) => {
      setState({
        showModalWallet: show
      })
    },
    setLoadingBalance: (value: boolean) => ({ setState }) => {
      setState({
        loadingBalance: value
      })
    },
    setLoadBalance: (value: boolean) => ({ setState }) => {
      setState({
        loadBalance: value
      })
    },
    setShowModal: (show: boolean) => ({ setState }) => {
      setState({
        showModal: show
      })
    },
    setSelectedWalletInfo: (wallet) => ({ setState }) => {
      setState({
        selectedWalletInfo: wallet || null
      })
    },
    setAddress: (address: string | null) => ({ setState }) => {
      setState({
        address: address || null
      })
    },
    setShowInstalled: (show: boolean) => ({ setState }) => {
      setState({
        showInstalled: show
      })
    },
    setBalanceBNB: (balanceBNB: number) => ({ setState }) => {
      setState({
        balanceBNB: Number(balanceBNB) || 0
      })
    },
    setBalanceBUSD: (balanceBUSD: number) => ({ setState }) => {
      setState({
        balanceBUSD: Number(balanceBUSD) || 0
      })
    },
    setWrongNetwork: (wrong: boolean) => ({ setState }) => {
      setState({
        wrongNetwork: wrong || false
      })
    },
    setChainId: (chainId: number) => ({ setState }) => {
      setState({
        chainId: chainId || 56
      })
    },
    clearProfile: () => ({ setState }) => {
      setState({
        address: '',
        balanceBNB: 0,
        balanceBUSD: 0,
        wrongNetwork: false,
        chainId: undefined,
      })
    }
  },
  name: 'StateWallet',
})

export const useHookStateWallet = createHook(StateWallet)
export const Container = createContainer(StateWallet, {
  onInit: () => ({ setState }, props) => {
    setState({ ...props })
  },
})

export const Subscriber = createSubscriber(StateWallet)
