import React, { memo } from "react";
import { useHookSider } from "state/sider";
import { Layout } from 'antd';
import { Wrapper } from "./styled";
import SVG from "components/SVG";
import { useLocation } from "react-router-dom";
import ConnectWallet from "components/ConnectWallet";
import { useHookStateWallet } from "state/wallet";

const { Header } = Layout;

const HeaderCpn = () => {

    const [state, actions] = useHookSider()
    const [wallet] = useHookStateWallet()
    const router = useLocation()

    const convertToTitle = (pathname) => {
        const title = pathname.replace(/[-/]/g, ' ');
        return title
            .split(' ')
            .map(word => word.charAt(0).toUpperCase() + word.slice(1))
            .join(' ');
    }

    return (
        <>
            <Wrapper collapse={state?.collapse}>
                <Header className='header'>
                    <div className="breadcrumb">
                        {
                            state?.canCollsapse
                                ? (
                                    <div onClick={() => {
                                        actions.setCollapse(!state?.collapse)
                                    }}>
                                        <SVG name="collapse" />
                                    </div>
                                ) : <></>
                        }
                        <div className="breadcrumb">
                            <span className="breadcrumb-child">Dashboards</span>
                            <span className="breadcrumb-child">/</span>
                            <span className="breadcrumb-child">{convertToTitle(router.pathname)}</span>
                        </div>
                    </div>
                    <div className="wallets">
                        <div className="info_coin">
                            <p className="amount">{wallet.balanceBUSD.toFixed(2)} BUSD</p>
                            <div className="icon_coin">
                                <SVG name="token-busd" />
                            </div>
                        </div>
                        <div className="info_network">
                            <div className="icon_network">
                                <SVG name="binance" />
                            </div>
                            <div className="__info_network">
                                <p className="network_name">Binance Smart Chain</p>
                                <p className="__network_name">Network</p>
                            </div>
                        </div>
                        <div className="actions_wallet">
                            <ConnectWallet />
                        </div>
                    </div>
                </Header>
            </Wrapper>
        </>
    )
}

export default memo(HeaderCpn)