
import { createStore, createHook, createContainer, createSubscriber } from 'react-sweet-state'

const LoadingConfirm = createStore({
  initialState: {
    show: false,
    title: '',
    imageLoad: null
  },
  actions: {
    setShow: (show: boolean) => ({ setState }) => {
      setState({
        show: show
      })
    },
    setTitle: (title: string) => ({ setState }) => {
      setState({
        title: title
      })
    },
    setImageLoad: (imageLoad: any) => ({ setState }) => {
      setState({
        imageLoad: imageLoad
      })
    }
  },
  name: 'LoadingConfirm',
})

export const useHookLoadingConfirm = createHook(LoadingConfirm)
export const Container = createContainer(LoadingConfirm, {
  onInit: () => ({ setState }, props) => {
    setState({ ...props })
  },
})

export const Subscriber = createSubscriber(LoadingConfirm)
