
import axios from 'axios';
import { apiKey } from '../../constants';
import { addressBUSD } from 'constants/busd';
import { NETWORK_BINANCE_URL } from 'constants/enum';

export const getTxhBUSDbyAccount = async (
  txHash,
  account,
  chainId
) => {

  const apiUrl = `${NETWORK_BINANCE_URL[`BSC_${chainId}`]}?module=account&action=tokentx&contractaddress=${addressBUSD[chainId]}&address=${account}&startblock=0&endblock=99999999&page=1&offset=100&sort=desc&apikey=${apiKey}`;

  return await axios
    .get(apiUrl)
    .then((response) => {
      const txList = response.data.result;
      const foundTx = txList.find(
        (tx) => tx.hash.toLowerCase() === txHash.toLowerCase()
      );
      if (foundTx) {
        return foundTx
      } else {
        return null
      }
    })
    .catch((error) => {
      return null
    });
}