import React, { useRef, useState } from "react";
import { ISortComponent } from "constants/interface";
import { IconSort, ItemSort, Sort, Sortes, Sorts, TextSort } from "./styled";
import { useRefCustom } from "hooks/useRefCustom";

export default function SortsComponent(data: ISortComponent) {

    const [showSort, setShowSort] = useState(false)

    const sortRef = useRef(null);

    useRefCustom(sortRef, setShowSort)

    return (
        <>
            <Sorts className="sort-component" ref={sortRef} onClick={() => setShowSort(!showSort)}>
                <Sort>
                    <TextSort>{data.dataActive.name}</TextSort>
                    <IconSort className="" src={require(`assets/images/svg/dropdown.svg`).default} alt="icon" />
                </Sort>
                <Sortes className={showSort ? 'active' : ''}>
                    {
                        data.list.map((item, key) => (
                            <ItemSort key={key} onClick={() => data.actionActive(item)}>{item.name}</ItemSort>
                        ))
                    }
                </Sortes>
            </Sorts>
        </>
    )
}