import React, { memo, useEffect, useState } from "react"
import {
    Body,
    Head,
    Header,
    Item,
    Items,
    TableSelection,
    Wrapper,
    Button
} from "./styled";
import { useMission } from "hooks/useMission";
import { useHookStateMission } from "state/mission";
import { generateArrayMission } from "utils/number";
import { useHookStateWallet } from "state/wallet";

const Table: React.FC<{ setAmount: any, setShow: any }> = ({ setAmount, setShow }) => {

    const { getMission } = useMission()
    const [state] = useHookStateMission()
    const [, actionWallet] = useHookStateWallet()
    const [missionIndex] = useState(generateArrayMission(12))

    const _getMission = async () => {
        await getMission().then(async () => {
            actionWallet.setLoadBalance(false)
            setTimeout(() => {
                actionWallet.setLoadBalance(true)
            }, 1000)
        })
    }

    useEffect(() => {
        _getMission()
        // eslint-disable-next-line
    }, [])

    const setAction = (amount) => {
        setAmount(amount)
        setShow(true)
    }

    return (
        <Wrapper className="width-scroll">
            <TableSelection className="no-sb">
                <Header>
                    <Head width="10%">#</Head>
                    <Head width="20%">Mission</Head>
                    <Head width="30%">Description</Head>
                    <Head width="35%">Action </Head>
                </Header>
                <Body>
                    {
                        missionIndex.map((item, index) => {
                            if (Number(state?.data?.done ?? 0) > item[0] - 1 && Number(state?.data?.done ?? 0) > item[1] - 1) {
                                return (
                                    <Items key={index}>
                                        <Item width="10%">#{index + 1}</Item>
                                        <Item width="20%">Target {index + 1}</Item>
                                        <Item width="30%">
                                            Sharing for $15 each time
                                        </Item>
                                        <Item width="35%">
                                            <Button className="btn-actions-complete">
                                                Completed
                                            </Button>
                                        </Item>
                                    </Items>
                                )
                            }
                            if (Number(state?.data?.done ?? 0) === item[1] - 1) {
                                return (
                                    <Items key={index}>
                                        <Item width="10%">#{index + 1}</Item>
                                        <Item width="20%">Target {index + 1}</Item>
                                        <Item width="30%">
                                            Sharing for $15 each time
                                        </Item>
                                        <Item width="35%">
                                            <Button onClick={() => setAction(10)} className="btn-actions">
                                                Send $10
                                            </Button>
                                        </Item>
                                    </Items>
                                )
                            }
                            if (Number(state?.data?.done ?? 0) === item[0] - 1) {
                                return (
                                    <Items key={index}>
                                        <Item width="10%">#{index + 1}</Item>
                                        <Item width="20%">Target {index + 1}</Item>
                                        <Item width="30%">
                                            Sharing for $15 each time
                                        </Item>
                                        <Item width="35%">
                                            <Button onClick={() => setAction(5)} className="btn-actions">
                                                Send $5
                                            </Button>
                                        </Item>
                                    </Items>
                                )
                            }

                            else {
                                return (
                                    <Items key={index}>
                                        <Item width="10%">#{index + 1}</Item>
                                        <Item width="20%">Target {index + 1}</Item>
                                        <Item width="30%">
                                            Sharing for $15 each time
                                        </Item>
                                        <Item width="35%">
                                            <Button className="btn-actions-disabled">
                                                Send
                                            </Button>
                                        </Item>
                                    </Items>
                                )
                            }
                        })
                    }
                </Body>
            </TableSelection>
        </Wrapper>
    )
}

export default memo(Table)