import { toast } from 'react-toastify';
import { keyLocalStorage } from '../constants'
import { removeLocalStorageServices, setLocalStorageServices } from 'utils/offlineServices'
import { ChangePasswordInterface, LoginInterface, RegisterInterface, VerifyEmailInterface, VerifyEmailInterfaceResetPassword } from 'constants/interface'
import { authApi } from 'config/api/auth';
import { handleParams } from 'utils';
import { useHookStateUser } from 'state/user';

export function useAuthentication() {

    const [, actions] = useHookStateUser()

    const login = async (values: LoginInterface) => {

        const promise = authApi.login(values)

        const loading = toast.loading("Pending...")

        return await promise.then(async (res) => {
            toast.dismiss(loading);
            setLocalStorageServices(keyLocalStorage.login, res.data.token)
            // history.push(AppRoute.workflow)
            return {
                success: true
            }
        }).catch(async (err) => {
            // return false
            toast.update(loading, { render: err.message, type: "error", autoClose: 5000, isLoading: false });
            return {
                ...err,
                error: true
            }
        })
    }

    const register = async (values: RegisterInterface) => {

        const promise = authApi.register(values)

        const loading = toast.loading("Pending...")

        return await promise.then(async (res) => {
            toast.dismiss(loading);
            return {
                success: true
            }
        }).catch(async (err) => {
            // return false
            toast.update(loading, { render: err.message, type: "error", autoClose: 5000, isLoading: false });
            return {
                ...err,
                error: true
            }
        })
    }

    const verify = async (values: VerifyEmailInterface) => {

        const query = await handleParams(values);

        const promise = authApi.verify(query)

        const loading = toast.loading("Pending...")

        return await promise.then(async (res) => {
            toast.dismiss(loading);
            return {
                success: true
            }
        }).catch(async (err) => {
            // return false
            toast.update(loading, { render: err.message, type: "error", autoClose: 5000, isLoading: false });
            return {
                ...err,
                error: true
            }
        })
    }

    const validateToken = async () => {
        return await authApi.validateToken().then(async (res) => {
            return true
        }).catch((err) => {
            return false
        })
    }

    const logout = async () => {
        return await authApi.logout().then(async (res) => {
            removeLocalStorageServices(keyLocalStorage.login)
            actions.clear()
            return true
        }).catch((err) => {
            return false
        })
    }

    const changePassword = async (values: ChangePasswordInterface) => {

        const promise = authApi.changePassword(values)

        const loading = toast.loading("Pending...")

        return await promise.then(async (res) => {
            toast.dismiss(loading);
            setLocalStorageServices(keyLocalStorage.login, res.data.token)
            return {
                success: true
            }
        }).catch(async (err) => {
            // return false
            toast.update(loading, { render: err.message, type: "error", autoClose: 5000, isLoading: false });
            return {
                ...err,
                error: true
            }
        })
    }

    const getInfo = async () => {
        const promise = authApi.getInfo()

        return await promise.then(async (res) => {
            actions.setData(res?.data?.user ?? null)
            return {
                success: true
            }
        }).catch(async (err) => {
            return {
                ...err,
                error: true
            }
        })
    }

    const forgotPassword = async (values) => {
        const promise = authApi.sendResetPassword(values)

        const loading = toast.loading("Pending...")

        return await promise.then(async (res) => {
            toast.dismiss(loading);
            return {
                success: true
            }
        }).catch(async (err) => {
            // return false
            toast.update(loading, { render: err.message, type: "error", autoClose: 5000, isLoading: false });
            return {
                ...err,
                error: true
            }
        })
    }

    const checkLinkReset = async (values: VerifyEmailInterface) => {
        const query = await handleParams(values);

        const promise = authApi.checkLinkReset(query)

        const loading = toast.loading("Pending...")

        return await promise.then(async (res) => {
            toast.dismiss(loading);
            return {
                success: true
            }
        }).catch(async (err) => {
            // return false
            toast.update(loading, { render: "Token reset invalid", type: "error", autoClose: 5000, isLoading: false });
            return {
                ...err,
                error: true
            }
        })
    }

    const resetPassword = async (values: VerifyEmailInterfaceResetPassword) => {
        const query = await handleParams(values);

        const promise = authApi.resetPassword(query, {
            newpassword: values.newpassword,
            repassword: values.repassword
        })

        const loading = toast.loading("Pending...")

        return await promise.then(async (res) => {
            toast.update(loading, { render: "Password reset success", type: "success", autoClose: 5000, isLoading: false });
            return {
                success: true
            }
        }).catch(async (err) => {
            // return false
            toast.update(loading, { render: "Password reset failed", type: "error", autoClose: 5000, isLoading: false });
            return {
                ...err,
                error: true
            }
        })
    }

    return {
        login,
        register,
        verify,
        validateToken,
        logout,
        changePassword,
        getInfo,
        forgotPassword,
        checkLinkReset,
        resetPassword
    }
}