import React, { memo, useEffect } from "react";
import { escapeRegExp, inputRegex } from "utils/number";
import { CurrentPage, Next, Prev, TotalPage, Wrapper } from "./styled";

const Pagination = (
    { state, actions }: { state?: any, actions?: any }
) => {

    const onPrev = () => {
        if (state?.currentPage > 1) {
            actions.setCurrentPage(state?.currentPage - 1)
        }
    }

    const onNext = () => {
        if (state?.currentPage < state?.totalPage) {
            actions.setCurrentPage(state?.currentPage + 1)
        }
    }

    useEffect(() => {
        if (state?.currentPage > state?.totalPage) {
            actions.setCurrentPage(Number(1))
        }
        // eslint-disable-next-line
    }, [state?.totalPage, state?.currentPage])

    return (
        <>
            <Wrapper>
                <Prev onClick={onPrev} src={require(`assets/images/svg/prev-page.svg`).default} />
                <CurrentPage onInput={(val: any) => {
                    if (val.target.value.replace(/,/g, ".") === "" || inputRegex.test(escapeRegExp(val.target.value.replace(/,/g, ".")))) {
                        if (Number(val.target.value) <= state?.totalPage && Number(val.target.value) >= 1) {
                            actions.setCurrentPage(Number(val.target.value))
                        }
                    }
                }} value={state?.currentPage} />
                <TotalPage>/ {state?.totalPage}</TotalPage>
                <Next onClick={onNext} src={require(`assets/images/svg/next-page.svg`).default} />
            </Wrapper>
        </>
    )
}

export default memo(Pagination)