
import { createStore, createHook, createContainer, createSubscriber } from 'react-sweet-state'

const Sider = createStore({
  initialState: {
    collapse: false,
    canCollsapse: true
  },
  actions: {
    setCollapse: (collapse: boolean) => ({ setState, getState }) => {
      if (getState().canCollsapse) {
        setState({
          collapse: collapse
        })
      }
    },
    setCanCollapse: (canCollsapse: boolean) => ({ setState }) => {
      setState({
        canCollsapse: canCollsapse
      })
    },
  },
  name: 'Sider',
})

export const useHookSider = createHook(Sider)
export const Container = createContainer(Sider, {
  onInit: () => ({ setState }, props) => {
    setState({ ...props })
  },
})

export const Subscriber = createSubscriber(Sider)
