import { AppRoute } from "config/router/config.router";
import { keyLocalStorage } from "../constants";
import { useAuthentication } from "hooks/useAuthentication";
import React, { memo, useEffect, useState } from "react";
import { Redirect, Route, RouteProps } from "react-router-dom";
import { getLocalStorageServices } from "utils/offlineServices";

const RouteLogin = (props: RouteProps) => {
    const [loged, setLoged] = useState(false)
    const { validateToken } = useAuthentication()

    const _validateToken = async () => {
        const token = await getLocalStorageServices(keyLocalStorage.login)
        if (token) {
            await validateToken().then(res => {
                if (!res) {
                    setLoged(false)
                } else {
                    setLoged(true)
                }
            })
        }
    }

    useEffect(() => {
        _validateToken()
        // eslint-disable-next-line
    }, [])

    return (
        <>
            {
                !loged
                    ? <Route {...props} />
                    : <Redirect to={AppRoute.overview} />
            }
        </>
    )

}

export default memo(RouteLogin)