import React, { memo } from "react"
import {
    Body,
    Head,
    Header,
    Item,
    Items,
    TableSelection,
    Wrapper,
    Button
} from "./styled";
import { useHookStateAffiliate } from "state/affiliate";
import { formatDate } from "utils/time";

const Table: React.FC = () => {

    const [state] = useHookStateAffiliate()

    return (
        <Wrapper className="width-scroll">
            <TableSelection className="no-sb">
                <Header>
                    <Head width="10%">#</Head>
                    <Head width="30%">User</Head>
                    <Head width="25%">Referral Date </Head>
                    <Head width="35%">Status </Head>
                </Header>
                <Body>
                    {
                        state?.dataList?.map((item, index) => (
                            <Items key={index}>
                                <Item width="10%">#{index + 1}</Item>
                                <Item width="30%">{item.info.email}</Item>
                                <Item width="25%">
                                    {formatDate(item.createdAt)}
                                </Item>
                                <Item width="35%">
                                    <Button className={`btn-actions ${item.active ? 'active' : 'inactive'}`}>
                                        {
                                            item.active
                                                ? 'Active'
                                                : 'InActive'
                                        }
                                    </Button>
                                </Item>
                            </Items>
                        ))
                    }
                </Body>
            </TableSelection>
        </Wrapper>
    )
}

export default memo(Table)