import { addressBUSD } from "constants/busd";
import React, { memo, useEffect } from "react";
import { useHookStateWallet } from "state/wallet";
import { useBalance } from "wagmi";

const Balance = ({ chainId, address }) => {

    const balanceWagmi = useBalance({
        addressOrName: address,
        chainId: chainId,
        // watch: true,
    })

    const balanceBUSD = useBalance({
        addressOrName: address,
        chainId: chainId,
        // watch: true,
        token: addressBUSD[chainId]
    })

    const [, actions] = useHookStateWallet()

    useEffect(() => {

        let balance = balanceWagmi?.data?.formatted

        actions.setBalanceBNB(
            Number(balance)
        )

        // eslint-disable-next-line
    }, [balanceWagmi?.data?.formatted])

    useEffect(() => {
        let balance = balanceBUSD?.data?.formatted
        actions.setBalanceBUSD(
            Number(balance)
        )

        // eslint-disable-next-line
    }, [balanceBUSD?.data?.formatted])

    return (
        <>

        </>
    )
}

export default memo(Balance)