import React, { memo, useEffect } from "react";
import { View, WrapForm } from "./styled";
import { Form, Input } from 'antd';
import AuthenNavigation from 'components/Navigration/authen'
import styles from '../styles.module.css'
import { useHistory, useLocation } from "react-router-dom";
import { useAuthentication } from "hooks/useAuthentication";
import { AppRoute } from "config/router/config.router";

const ResetPassword = () => {
    const [formResetPass] = Form.useForm();
    const history = useHistory()
    let { search } = useLocation();
    const { checkLinkReset, resetPassword } = useAuthentication()

    const onFinishReset = async () => {
        const fileds = formResetPass.getFieldsValue()
        const query = new URLSearchParams(search);
        const token = query.get('token');
        const account = query.get('account');
        await resetPassword({
            ...fileds,
            token,
            account
        }).then((res) => {
            if (res.success) {
                history.push(AppRoute.login)
            }
        })
    };

    const compareToFirstPassword = (_, value) => {
        const password = formResetPass.getFieldValue('newpassword');
        if (value && password !== value) {
            return Promise.reject(new Error('Passwords do not match!'));
        }
        return Promise.resolve();
    };

    const onCheckLinkReset = async () => {
        const query = new URLSearchParams(search);
        const token = query.get('token');
        const account = query.get('account');
        await checkLinkReset({ token, account }).then((res) => {
            if (res.error) {
                history.push(AppRoute.login)
            }
        })
    };

    useEffect(() => {
        if (search) {
            onCheckLinkReset()
        }
        // eslint-disable-next-line
    }, [])

    return (
        <>
            <View>
                <AuthenNavigation />
                <WrapForm>
                    <Form
                        name="basic"
                        form={formResetPass}
                        className={`${styles.smt_form}`}
                        onFinish={onFinishReset}
                        autoComplete="off"
                    >
                        <p className={styles.smt_titleForm}>Reset Password</p>
                        <p className={styles.smt_descriptionForm}>Enter your new password.</p>
                        <Form.Item
                            label="New Password"
                            name="newpassword"
                            rules={[{ required: true, message: 'Please input your new password!' }]}
                        >
                            <Input.Password className={styles.formInput} placeholder="min 8 characters" />
                        </Form.Item>
                        <Form.Item
                            label="Confirm Password"
                            name="repassword"
                            dependencies={['newpassword']}
                            rules={[
                                {
                                    required: true, message: 'Please input your confirm password!'
                                },
                                {
                                    validator: compareToFirstPassword
                                }
                            ]}
                        >
                            <Input.Password className={styles.formInput} placeholder="min 8 characters" />
                        </Form.Item>

                        <Form.Item>
                            <button className={styles.smt_buttonSubmit} type="submit">
                                Submit
                            </button>
                        </Form.Item>
                    </Form>
                </WrapForm>
            </View >
        </>
    )
}

export default memo(ResetPassword)