import { memo } from "react"
import styles from './styles.module.css'
import { Form, Input } from "antd"
import { View, WrapForm } from "./styled";
import { useAuthentication } from "hooks/useAuthentication";
import { AppRoute } from "config/router/config.router";
import { useHistory } from "react-router-dom";

const Mission = () => {

  const { changePassword, logout } = useAuthentication()
  const history = useHistory()
  const [formChangePass] = Form.useForm();

  const onFinishCreate = async () => {
    const fields = formChangePass.getFieldsValue()
    await changePassword(fields).then(async (res) => {
      if (res.success) {
        await logout().then(() => history.push(AppRoute.login))
      }
    })
  };

  const compareToFirstPassword = (_, value) => {
    const password = formChangePass.getFieldValue('newpassword');
    if (value && password !== value) {
      return Promise.reject(new Error('Passwords do not match!'));
    }
    return Promise.resolve();
  };

  return (
    <>
      <View>
        <WrapForm>
          <p className={`font-600 text-14`} style={{ marginBottom: '10px' }}>Change Password</p>
          <p className={`font-400 text-12 ${styles.title}`} style={{ opacity: 0.4, marginBottom: '42px' }}>Please enter your details.</p>
          <Form
            name="basic"
            form={formChangePass}
            className={`${styles.smt_form}`}
            onFinish={onFinishCreate}
            autoComplete="off"
          >
            <Form.Item
              label="Old Password"
              name="oldpassword"
              rules={[{ required: true, message: 'Please input your old password!' }]}
            >
              <Input.Password className={styles.formInput} placeholder="min 8 characters" />
            </Form.Item>
            <Form.Item
              label="New Password"
              name="newpassword"
              rules={[{ required: true, message: 'Please input your new password!' }]}
            >
              <Input.Password className={styles.formInput} placeholder="min 8 characters" />
            </Form.Item>
            <Form.Item
              label="Confirm Password"
              name="repassword"
              dependencies={['newpassword']}
              rules={[
                {
                  required: true, message: 'Please input your confirm password!'
                },
                {
                  validator: compareToFirstPassword
                }
              ]}
            >
              <Input.Password className={styles.formInput} placeholder="min 8 characters" />
            </Form.Item>
            <Form.Item>
              <button className={styles.smt_buttonSubmit} type="submit">
                Submit
              </button>
            </Form.Item>
          </Form>
        </WrapForm>
      </View>
    </>
  )
}

export default memo(Mission)