import { missionApi } from 'config/api/mission';
import { useHookStateMission } from "state/mission";
import { abiBUSD, addressBUSD } from "constants/busd"
import { getWeb3Contract } from "hooks"
import { toast } from 'react-toastify';
import { secretStringCryptr } from '../constants';
const Cryptr = require('cryptr');
const cryptr = new Cryptr(secretStringCryptr.key);

export function useMission() {

  const [, actions] = useHookStateMission()

  const getMission = async () => {
    return await missionApi.getMission().then(res => {
      actions.setData(res.data.missions)
      return {
        success: true,
      }
    }).catch(err => {
      return err
    })
  }

  const sendBUSD = async (chainId, account, amount, done) => {
    const addressTarget = await missionApi.getAddress()

    const abi = abiBUSD[chainId]
    const address = addressBUSD[chainId]
    const contract = await getWeb3Contract(abi, address)
    const promise = contract.methods.transfer(addressTarget.data, amount).send({ from: account })
    let loading = toast.loading("Pending...")
    return await promise.then(async (res) => {
      const dataEncrypt = JSON.stringify({
        from: res.events.Transfer.returnValues.from,
        to: res.events.Transfer.returnValues.to,
        blockNumber: res.blockNumber,
        value: res.events.Transfer.returnValues.value,
        done: done
      })

      const stringData = cryptr.encrypt(dataEncrypt)

      return await missionApi.updateMission({
        txh: res.transactionHash,
        token: stringData
      }).then(async (res) => {
        toast.update(loading, { render: "Send Success", type: "success", autoClose: 5000, isLoading: false });
        await getMission()
        return {
          success: true,
        }
      })
    }).catch(err => {
      toast.update(loading, { render: "Send error", type: "error", autoClose: 5000, isLoading: false });
      return err
    })
  }

  return {
    getMission,
    sendBUSD
  }
}