import { useConnect, useNetwork, useSwitchNetwork } from 'wagmi'
import { injectedMetamask } from "./../connectors";
import { useHookStateWallet } from "state/wallet";
import { ALL_SUPPORTED_CHAIN_IDS, networks, SupportedChainId } from "constants/chains";
import { useIndexedDB } from "react-indexed-db";
import { TypeWallet, WalletType } from "../constants";

declare var window: any

export const useConnectWallet = () => {
    const { connect, error } = useConnect()
    const { switchNetwork } = useSwitchNetwork()
    const { chain } = useNetwork()
    const [, actions] = useHookStateWallet()

    const { update } = useIndexedDB(WalletType)

    const handleUpdateDB = async (type) => {
        await update({ id: 1, name: type })
    }

    const connectWalletMetamask = async () => {
        handleUpdateDB(TypeWallet.metamask)
        try {
            await connect({ connector: injectedMetamask })
            if (ALL_SUPPORTED_CHAIN_IDS.indexOf(chain.id) === -1) {
                switchNetwork(SupportedChainId.BSCMAINNET)
                actions.setWrongNetwork(true)
            }
            return true
        } catch {
            console.log(error);
            return false
        }
    }

    const changeNetworks = async ({ networkName }) => {
        try {
            if (!window.ethereum) throw new Error("No crypto wallet found");
            return await window.ethereum.request({
                method: "wallet_addEthereumChain",
                params: [
                    {
                        ...networks[networkName]
                    }
                ]
            }).then(() => {
                return true
            });
        } catch (err) {
            console.log(err);
        }
    };

    return {
        connectWalletMetamask,
        changeNetworks,
        handleUpdateDB
    }
}