import { createClient, configureChains } from 'wagmi'
import { InjectedConnector as InjectedConnectorWagmi } from 'wagmi/connectors/injected'
import { jsonRpcProvider } from 'wagmi/providers/jsonRpc'
import { CHAINS } from './chains'
import { MetaMaskConnector } from 'wagmi/connectors/metaMask'

export const { provider, chains } = configureChains(
  [...CHAINS],
  [
    jsonRpcProvider({
      rpc: (chain) => {
        return { http: chain.rpcUrls.default }
      },
    }),
  ],
)

export const injectedMetamask = new MetaMaskConnector({
  chains,
  options: {
    shimDisconnect: false,
    shimChainChangedDisconnect: true,
  },
})

export const injectedConnector = new InjectedConnectorWagmi({
  chains,
  options: {
    name: 'Name',
  },
})

export const client = createClient({
  autoConnect: true,
  provider,
  connectors: [injectedConnector],
})
