import { affiliateApi } from 'config/api/affiliate';
import { useHookStateAffiliate } from 'state/affiliate';

export function useAffiliate() {

  const [, actions] = useHookStateAffiliate()

  const getRefCode = async () => {
    return await affiliateApi.getRefCode().then(res => {

      actions.setAffiliate(res.data)
      return {
        success: true,
      }
    }).catch(err => {
      return err
    })
  }

  const getComunity = async () => {
    return await affiliateApi.getComunity().then(res => {

      actions.setData(res.data.comunity)

      return {
        success: true,
      }
    }).catch(err => {
      return err
    })
  }

  return {
    getRefCode,
    getComunity
  }
}