import React, { useEffect, useRef, useState } from 'react'
import { useWeb3React } from '@web3-react/core'
import {
    Logout,
    Wrapper
} from './styled'
import Balance from './balance'
import { shortenAddress } from 'utils'
import { useConnectWallet } from 'hooks/useConnectWallet'
import { useHookStateWallet } from 'state/wallet'
import { useIndexedDB } from 'react-indexed-db'
import { useAccount, useNetwork, useDisconnect } from 'wagmi'
import { WalletType } from '../../constants'
import { ALL_SUPPORTED_CHAIN_IDS } from 'constants/chains'
import SVG from 'components/SVG'
import { useRefCustom } from 'hooks/useRefCustom'

declare var window: any

export default function ConnectWallet() {

    const { chainId, account } = useWeb3React();
    const {
        connectWalletMetamask,
        changeNetworks,
        handleUpdateDB
    } = useConnectWallet();
    const { address } = useAccount()
    const { chain } = useNetwork()
    const { disconnect } = useDisconnect()
    const [state, actions] = useHookStateWallet()
    const [showLogout, setShowLogout] = useState(false)
    const { getByID } = useIndexedDB(WalletType);
    const infoRef = useRef(null);

    const handleClickOutside = (event, ref, set) => {
        if (ref.current && !ref.current.contains(event.target)) {
            set(false);
        }
    }

    useEffect(() => {
        document.addEventListener("click", (e) => handleClickOutside(e, infoRef, actions.setShowModal));
        return () => {
            document.removeEventListener("click", (e) => handleClickOutside(e, infoRef, actions.setShowModal));
        };
        // eslint-disable-next-line 
    }, [infoRef]);

    const onConnect = async () => {
        await connectWalletMetamask().then(async (res) => {
            if (res === false) {
                await changeNetworks({ networkName: 'bsc' })
            } else {
                actions.setShowModal(false)
            }
        })
    }

    const onDisconnect = async () => {
        await disconnect()
        await handleUpdateDB('')
        actions.clearProfile()
    }

    if (window.ethereum) {
        window.ethereum.on('chainChanged', async (chainId) => {
            window.location.reload();
        });

        window.ethereum.on('accountsChanged', (chainId: string) => {
            window.location.reload();
        });
    }

    const onCallConnect = async () => {
        await actions.clearProfile()
        await getByID(1).then(async res => {
            if (res && res.name) {
                if (!address) {
                    // Account not exits auto connect coinbase, walletconnect, trust, metamask
                    await onConnect
                } else {
                    // If connect set variable
                    actions.setShowModal(false)
                    if (ALL_SUPPORTED_CHAIN_IDS.indexOf(chain.id) === -1) {
                        actions.setWrongNetwork(true)
                    } else {
                        actions.setWrongNetwork(false)
                    }
                    actions.setAddress(address)
                    actions.setChainId(chain?.id)
                }
            }
        })
    }

    useEffect(() => {
        onCallConnect()
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [chainId, chain, account, address]);

    useEffect(() => {
        actions.setShowModalWallet(state.showModal)
        if (state.showModal) {
            // Add remove scroll to body elementor
            const elm = document.querySelector('body')
            elm.classList.add('no-scroll')
        } else {
            // Add class scroll to body elementor
            const elm = document.querySelector('body')
            elm.classList.remove('no-scroll')
        }
        // eslint-disable-next-line
    }, [state.showModal])


    const connectRef = useRef(null);

    useRefCustom(connectRef, setShowLogout)

    return (
        <>
            <Wrapper className='wrap-wallet' bgWallet={state.wrongNetwork} ref={infoRef}>
                {state.address && !state?.wrongNetwork ? (
                    <div
                        ref={connectRef}
                        className="_actions_wallet"
                        onClick={() => {
                            setShowLogout(!showLogout)
                        }}
                    >
                        <div className="icon_wallet">
                            <SVG name="metamask" />
                        </div>
                        <div className="__text">{shortenAddress(state.address ?? '')}</div>
                        <Logout
                            className={state?.chainId && state?.address && showLogout ? 'active' : ''}
                            onClick={() => {
                                if (state?.chainId && state?.address && showLogout) {
                                    onDisconnect()
                                }
                            }}
                        >
                            <p>Log out</p>
                            <SVG name="logout" />
                        </Logout>
                    </div>
                ) : (
                    <div className="_actions_wallet"
                        onClick={onConnect}
                    >
                        <div className="icon_wallet">
                            <SVG name="metamask" />
                        </div>
                        <div className="__text">{state.wrongNetwork ? 'Wrong Network' : 'Connect Wallet'}</div>
                    </div>
                )}
                {
                    state?.chainId && state?.address && state?.loadBalance
                        ? (
                            <Balance chainId={state?.chainId} address={state?.address} />
                        ) : (
                            <></>
                        )
                }
            </Wrapper>
        </>
    )
}