import React, { memo } from "react";
import styles from './styles.module.css'
import { ethers } from "ethers";
import { Form } from "antd";
import { useHookStateWallet } from "state/wallet";
import ConnectWallet from "components/ConnectWallet";
import styled from "styled-components";
import { useAccount } from "hooks/useAccount";
import { handleNumberBC } from "utils";
import { feeJoinPlaftform } from "../../../constants";
import { useAuthentication } from "hooks/useAuthentication";
import { useHookLoadingConfirm } from "components/LoadingConfirm/state";

const Action = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;

  .wrap-wallet {
    width: 100%;

    ._actions_wallet {
      width: 100% !important;
      padding: 15.5px 0px;
      box-sizing: border-box;
      height: unset;
      .__text {
        font-weight: 600;
        font-size: 14px;
      }

      svg {
        height: 22px;
      }
    }
  }
`;

const PopupActivePlaftform: React.FC = () => {
  const [stateWallet, actionWallet] = useHookStateWallet()
  const { activePlaftform } = useAccount()
  const { getInfo } = useAuthentication()
  const [stateConfirm, actionsConfirm] = useHookLoadingConfirm()

  const _activePlaftform = async () => {
    if (!stateConfirm.show) {
      if (stateWallet?.address && stateWallet?.chainId) {
        const number = await handleNumberBC(Number(feeJoinPlaftform))
        const value = ethers.BigNumber.from(number)
        actionsConfirm.setShow(true)
        actionsConfirm.setTitle('Active Plaftform')
        await activePlaftform(stateWallet?.chainId, stateWallet?.address, value).then(async (res) => {
          if (res.success) {
            await getInfo()
            actionWallet.setLoadBalance(false)
            setTimeout(() => {
              actionWallet.setLoadBalance(true)
            }, 1000)
          }
          actionsConfirm.setShow(false)
        }).catch(() => {
          actionsConfirm.setShow(false)
        })
      }
    }
  }

  return (
    <>
      <div className={styles.wrapper}>
        <div className={styles.showPopup}>
          <Form
            name="basic"
            className={`${styles.smt_form}`}
            autoComplete="off"
          >
            <p style={{ textAlign: 'center' }} className={styles.smt_titleForm}>Active account</p>
            <p style={{ textAlign: 'center' }} className={styles.smt_descriptionForm}>Only with ${feeJoinPlaftform} BUSD for join platform</p>
            <Action className={styles.actions}>
              {
                stateWallet?.address && stateWallet.chainId
                  ? (
                    stateWallet?.balanceBUSD < feeJoinPlaftform
                      ? <p className={styles.smt_buttonSubmit}>
                        Insufficient balance
                      </p>
                      : <p onClick={_activePlaftform} className={styles.smt_buttonSubmit}>
                        Accept
                      </p>
                  ) : (
                    <ConnectWallet />
                  )
              }
            </Action>
          </Form>
        </div>
      </div>
    </>
  )
}

export default memo(PopupActivePlaftform)