import { abiBUSD, addressBUSD } from "constants/busd"
import { getWeb3Contract } from "hooks"
import { toast } from 'react-toastify';
import { addressActivePlaftform } from '../constants';
import { userApi } from 'config/api/user';
import { useHookStateUser } from "state/user";
import { getTxhBUSDbyAccount } from "config/api/binance";

export function useAccount() {

  const [, actions] = useHookStateUser()

  const activePlaftform = async (chainId, account, value) => {
    const abi = abiBUSD[chainId]
    const address = addressBUSD[chainId]
    const contract = await getWeb3Contract(abi, address)
    const promise = contract.methods.transfer(addressActivePlaftform, value).send({ from: account })

    const loading = toast.loading("Pending...")

    return await promise.then(async (res) => {

      let txh = null
      let count = 0

      do {
        txh = await getTxhBUSDbyAccount(res.transactionHash, addressActivePlaftform, chainId)
        count = count + 1
      } while (!txh && count < 200)

      return await userApi.activePlaftform({
        account: account,
        txh: res.transactionHash
      }).then(res => {
        toast.update(loading, { render: "Active Success", type: "success", autoClose: 5000, isLoading: false });
        return {
          success: true,
        }
      }).catch(err => {
        toast.update(loading, { render: err.message, type: "error", autoClose: 5000, isLoading: false });
      })
    }).catch(err => {
      toast.update(loading, { render: err.message, type: "error", autoClose: 5000, isLoading: false });
    })
  }

  const connectWallet = async (account) => {
    const promise = userApi.connectWallet({ address: account })

    const loading = toast.loading("Pending...")

    return await promise.then(async (res) => {
      toast.update(loading, { render: "Active Success", type: "success", autoClose: 5000, isLoading: false });
      return {
        success: true
      }
    }).catch(err => {
      toast.update(loading, { render: err.message, type: "error", autoClose: 5000, isLoading: false });
      return {
        ...err,
        error: false
      }
    })
  }

  const getBalance = async () => {
    return await userApi.getBalance().then(res => {

      actions.setBalance(res.data)
      return {
        success: true,
      }
    }).catch(err => {
      return err
    })
  }

  return {
    activePlaftform,
    connectWallet,
    getBalance
  }
}