import React, { memo, useEffect, useState } from "react";
import { View, WrapForm } from "./styled";
import { Form } from 'antd';
import AuthenNavigation from 'components/Navigration/authen'
import styles from '../styles.module.css'
import { NavLink, useLocation } from "react-router-dom";
import { useAuthentication } from "hooks/useAuthentication";
import { AppRoute } from "config/router/config.router";

const Login = () => {
    const { verify } = useAuthentication()
    let { search } = useLocation();
    const [loading, setLoading] = useState(true)
    const [error, setError] = useState(null)

    const onVerifyEmail = async () => {
        const query = new URLSearchParams(search);
        const token = query.get('token');
        const account = query.get('account');
        await verify({ token, account }).then((res) => {
            if (res.success) {
                setLoading(false)
            } else {
                setError({ ...res })
                setLoading(false)
            }
        })
    };

    useEffect(() => {
        if (search) {
            onVerifyEmail()
        }
        // eslint-disable-next-line
    }, [])

    return (
        <>
            <View>
                <AuthenNavigation />
                <WrapForm>
                    <Form
                        name="basic"
                        className={`${styles.smt_form}`}
                        autoComplete="off"
                    >
                        <p style={{ textAlign: 'center' }} className={styles.smt_titleForm}>{loading ? 'Verification' : error ? 'Verification failed' : 'Thanks you!'}</p>
                        {
                            loading
                                ? <div style={{ marginTop: '20px' }}>
                                    <div className={styles.loading} />
                                </div>
                                : (
                                    <>
                                        {
                                            error
                                                ? <p style={{ textAlign: 'center' }} className={styles.smt_descriptionForm}>{error.message}</p>
                                                : <p style={{ textAlign: 'center' }} className={styles.smt_descriptionForm}>Congratulations on your successful verification!</p>
                                        }
                                        <NavLink to={AppRoute.login} className={styles.smt_buttonSubmit}>
                                            Login
                                        </NavLink>
                                    </>
                                )
                        }
                    </Form>
                </WrapForm>
            </View >
        </>
    )
}

export default memo(Login)