import { memo, useEffect, useState } from "react"
import styles from './styles.module.css'
import SortsComponent from "components/Sorts"
import Table from "./components/table"
import { VALUE_SORT_TRANSACTION } from "constants/constants"
import Pagination from "components/Pagination";
import { useHookTransaction } from "./state"
import { useHookStateWallet } from "state/wallet"
import { useTransaction } from "hooks/useTransaction"

const Transaction = () => {

  const [note, setNote] = useState(VALUE_SORT_TRANSACTION[0])
  const [state, actions] = useHookTransaction()
  const [stateWallet] = useHookStateWallet()
  const { getListTransaction } = useTransaction()

  const _getListTransaction = async () => {
    await getListTransaction(note.value)
  }

  useEffect(() => {
    if (stateWallet.chainId && stateWallet?.address) {
      _getListTransaction();
    }
    // eslint-disable-next-line
  }, [stateWallet.chainId, state?.currentPage, note])

  return (
    <>
      <div id="transaction" className="transaction-page">
        <p className={`font-600 text-14 ${styles.title}`}>Transaction History</p>
        <div className={styles.sort}>
          <SortsComponent
            dataActive={note}
            actionActive={setNote}
            list={VALUE_SORT_TRANSACTION}
          />
        </div>
        <Table />

        <div>
          <Pagination
            state={state}
            actions={actions}
          />
        </div>
      </div>
    </>
  )
}

export default memo(Transaction)