import React, { memo } from "react";
import styles from './styles.module.css'
import { Form } from "antd";
import styled from "styled-components";
import { useAccount } from "hooks/useAccount";
import { useHookStateWallet } from "state/wallet";
import { useAuthentication } from "hooks/useAuthentication";

const Action = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;

  .wrap-wallet {
    width: 100%;

    ._actions_wallet {
      width: 100% !important;
      padding: 15.5px 0px;
      box-sizing: border-box;
      height: unset;
      .__text {
        font-weight: 600;
        font-size: 14px;
      }

      svg {
        height: 22px;
      }
    }
  }
`;

const ActiveWallet = () => {

    const { connectWallet } = useAccount()
    const { getInfo } = useAuthentication()
    const [stateWallet] = useHookStateWallet()

    const _connectWallet = async () => {
        if (stateWallet.address && stateWallet?.chainId) {
            await connectWallet(stateWallet?.address).then(async (res) => {
                if (res.success) {
                    await getInfo()
                }
            })
        }
    }

    return (
        <>
            <div className={styles.wrapper}>
                <div className={styles.showPopup}>
                    <Form
                        name="basic"
                        className={`${styles.smt_form}`}
                        autoComplete="off"
                    >
                        <p style={{ textAlign: 'center' }} className={styles.smt_titleForm}>Active Wallet</p>
                        <p style={{ textAlign: 'center', marginBottom: 0 }} className={styles.smt_descriptionForm}>Confirm this is your official address for all future transactions.</p>
                        <p style={{ textAlign: 'center', color: '#FF7171' }} className={styles.smt_descriptionForm}>
                            <span className="font-600" style={{ color: '#FF7171', textDecoration: 'underline' }}>NOTE:&nbsp;</span>&nbsp;After activation, it is not possible to change the wallet address
                        </p>
                        <Action className={styles.actions} onClick={_connectWallet}>
                            <p className={styles.smt_buttonSubmit}>
                                Active Wallet
                            </p>
                        </Action>
                    </Form>
                </div>
            </div>
        </>
    )
}

export default memo(ActiveWallet)