
import { createStore, createHook, createContainer, createSubscriber } from 'react-sweet-state'

const StateAffiliate = createStore({
  initialState: {
    data: null,
    dataList: [],
    affiliate: null,
    currentPage: 1,
    totalPage: 0,
  },
  actions: {
    setData: (data) => ({ setState }) => {
      setState({
        data: data
      })
    },
    setAffiliate: (affiliate) => ({ setState }) => {
      setState({
        affiliate: affiliate
      })
    },
    setCurrentPage: (currentPage) => ({ setState }) => {
      setState({
        currentPage: currentPage
      })
    },
    setTotalPage: (totalPage) => ({ setState }) => {
      setState({
        totalPage: totalPage
      })
    },
    setDataList: (dataList) => ({ setState }) => {
      setState({
        dataList: [...dataList]
      })
    },
    clear: () => ({ setState }) => {
      setState({
        data: null,
      })
    }
  },
  name: 'StateAffiliate',
})

export const useHookStateAffiliate = createHook(StateAffiliate)
export const Container = createContainer(StateAffiliate, {
  onInit: () => ({ setState }, props) => {
    setState({ ...props })
  },
})

export const Subscriber = createSubscriber(StateAffiliate)
