export const formatNumbersLess10 = (num: Number) => {
    return Number(num) < 10 ? `0${num}` : num;
}

export const splitNumberForImage = (num: any) => {
    const numStr = num.toString()
    return numStr.split("").map(Number)
}

export function isNumber(n) { return /^-?[\d.]+(?:e-?\d+)?$/.test(n); }

export const inputRegex = RegExp(`^\\d*(?:\\\\[.])?\\d*$`);

export function escapeRegExp(string: string): string {
    return string.replace(/[.*+?^${}()|[\]\\]/g, '\\$&'); // $& means the whole matched string
}

export const nFormatter = (num: number, digits: number) => {
    const lookup = [
        { value: 1, symbol: '' },
        { value: 1e3, symbol: 'k' },
        { value: 1e6, symbol: 'M' },
        { value: 1e9, symbol: 'G' },
        { value: 1e12, symbol: 'T' },
        { value: 1e15, symbol: 'P' },
        { value: 1e18, symbol: 'E' },
    ];
    const item = lookup
        .slice()
        .reverse()
        .find(function (item) {
            return num >= item.value;
        });
    // return item ? (num / item.value).toFixed(digits).replace(rx, '$1') + item.symbol : '0';
    return item ? (num / item.value).toFixed(digits) + item.symbol : '0';
};

export const generateArrayMission = (numElements) => {
    const result = [];
    let currentRow = [];

    for (let i = 1; i <= numElements * 2; i++) {
        currentRow.push(i);

        if (i % 2 === 0) {
            result.push(currentRow);
            currentRow = [];
        }
    }

    return result;
}

export const abbreviateNumber = (number) => {
    const SI_SYMBOL = ['', 'K', 'M', 'B', 'T'];

    const tier = Math.log10(Math.abs(number)) / 3 | 0;

    const abbreviated = (number / Math.pow(10, tier * 3)).toFixed(1);

    const symbol = SI_SYMBOL[tier];

    return `${abbreviated}${symbol}`;
}
