import { NODE_URL } from '../constants';
import Web3 from 'web3'
import { JsonRpcSigner, Web3Provider } from '@ethersproject/providers';
import { ethers } from "ethers";

export function getWeb3Contract(abi: any, address: string) {
    const web3 = new Web3(Web3.givenProvider);
    const contract = new web3.eth.Contract(abi, address)
    return contract
}

export async function getEtherContractProxy(abi: any, addressContract: string, addressProxy: string, library?: any, chainId = 97, account?: any) {
    const PROVIDER = new ethers.providers.JsonRpcProvider(NODE_URL[chainId]);
    const contractCall = new ethers.Contract(addressContract, abi, getProviderOrSigner(library, account) ?? PROVIDER);
    const contract = await contractCall.attach(addressProxy);
    return contract
}

export function getSigner(
    library: Web3Provider,
    account: string,
): JsonRpcSigner {
    return library.getSigner(account).connectUnchecked();
}

export function getProviderOrSigner(
    library: Web3Provider,
    account?: string,
): Web3Provider | JsonRpcSigner {
    return account ? getSigner(library, account) : library;
}