import { formatNumbersLess10 } from "./number";
const moment = require('moment');

export const ConvertTimeX1000 = (timestamp: number) => {
    const YYYY = (new Date(timestamp).getFullYear());
    const MM = formatNumbersLess10((new Date(timestamp).getMonth()) + 1);
    const DD = formatNumbersLess10(new Date(timestamp).getDate());
    const hh = formatNumbersLess10(new Date(timestamp).getHours());
    const mm = formatNumbersLess10(new Date(timestamp).getMinutes());
    const ss = formatNumbersLess10(new Date(timestamp).getSeconds());

    return `${YYYY}-${MM}-${DD} ${hh}:${mm}:${ss}`;
}

export const ConvertTimeX1000Date = (timestamp: number) => {
    const YYYY = (new Date(timestamp).getFullYear());
    const MM = formatNumbersLess10((new Date(timestamp).getMonth()) + 1);
    const DD = formatNumbersLess10(new Date(timestamp).getDate());

    return `${YYYY}-${MM}-${DD}`;
}

export const formatDate = (date) => {
    if (date) {
        const formattedTime = moment.utc(date);

        const diffDays = moment().diff(formattedTime, 'days');

        if (diffDays === 0) {
            return `Today, ${formattedTime.format('HH:mm')}`;
        } else if (diffDays === 1) {
            return `Yesterday, ${formattedTime.format('HH:mm')}`;
        } else {
            return formattedTime.format('DD/MM/YYYY, HH:mm');
        }
    } else {
        return '';
    }
}
