import { transactionApi } from 'config/api/transaction';
import { handleParams } from 'utils';
import { useHookTransaction } from 'views/Transaction/state';

export function useTransaction() {

    const [stateTransaction, actionsTransaction] = useHookTransaction()

    const getListTransaction = async (note) => {
        const query = await handleParams({ page: stateTransaction?.currentPage, note: note });

        return await transactionApi.getTransaction(query).then(async (res) => {
            if (res && res.data) {
                actionsTransaction.setData(res.data.docs)
                actionsTransaction.setTotalPage(Number(res.data.pages))
            }
            return {
                success: true
            }
        }).catch(async (err) => {
            return {
                ...err,
                error: true
            }
        })
    }

    return {
        getListTransaction
    }
}