
import { createStore, createHook, createContainer, createSubscriber } from 'react-sweet-state'

const StateMission = createStore({
  initialState: {
    data: null,
  },
  actions: {
    setData: (data) => ({ setState }) => {
      setState({
        data: data
      })
    },
    clear: () => ({ setState }) => {
      setState({
        data: null,
      })
    }
  },
  name: 'StateMission',
})

export const useHookStateMission = createHook(StateMission)
export const Container = createContainer(StateMission, {
  onInit: () => ({ setState }, props) => {
    setState({ ...props })
  },
})

export const Subscriber = createSubscriber(StateMission)
