import { memo, useEffect } from "react"
import styles from './styles.module.css'
import Table from "./components/table"
import { useHookStateAffiliate } from "state/affiliate"
import { useAffiliate } from "hooks/useAffiliate"
import Copy from "components/Copy"
import SVG from "components/SVG"
import { NavLink } from "react-router-dom"
import { AppRoute } from "config/router/config.router"
import Pagination from "components/Pagination"

const itemPerpage = 10

const Comunity = () => {

  const [state, actions] = useHookStateAffiliate()
  const { getRefCode, getComunity } = useAffiliate()

  const _getRefCode = async () => {
    await getRefCode()
  }

  const _getComunity = async () => {
    await getComunity()
  }

  useEffect(() => {
    _getComunity()
    // eslint-disable-next-line
  }, [])

  useEffect(() => {
    if (!state?.affiliate) {
      _getRefCode()
    }
    // eslint-disable-next-line
  }, [state?.affiliate])

  useEffect(() => {
    if (state?.data) {
      const startIndex = (state?.currentPage - 1) * itemPerpage;
      const endIndex = startIndex + itemPerpage;
      actions.setDataList(state?.data.slice(startIndex, endIndex))
      actions.setTotalPage(Math.ceil(state?.data.length / itemPerpage))
    }
    // eslint-disable-next-line
  }, [state?.currentPage, state?.data])

  return (
    <>
      <div id="comunity" className="comunity-page">
        {
          state?.affiliate?.active
            ? <p className={`font-600 text-14 ${styles.title}`}>Referral Link</p>
            : <></>
        }
        <div className={styles.wrapFilter}>
          {
            state?.affiliate?.active
              ? (
                <div className={styles.filter}>
                  <input disabled className={styles.search} value={`https://sm.app/#/code/${state?.affiliate?.refCode}` ?? ''} />
                  <div className={styles.copy}>
                    <Copy isRight={false} toCopy={`https://sm.app/#/code/${state?.affiliate?.refCode}`}>
                      <SVG name="copy" />
                    </Copy>
                  </div>
                </div>
              ) : (
                <></>
              )
          }
          {
            state?.affiliate?.active
              ? <></>
              : <p className={styles.noUnlockRefCode}>* Complete mission 1 to unlock referral link <NavLink className={styles.linkMission} to={AppRoute.mission}>Complete now</NavLink></p>
          }
        </div>
        <p className={`font-600 text-14 ${styles.title}`}>Total Member ({state.data?.length})</p>
        <Table />
        <div>
          <Pagination
            state={state}
            actions={actions}
          />
        </div>
      </div>
    </>
  )
}

export default memo(Comunity)