import React, { memo } from "react";
import { ReactSVG } from 'react-svg'
import styled from "styled-components";

type Props = {
  name: string;
  path?: string;
  class?: string;
}

const Wrapper = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;

  * {
    display: flex;
    align-items: center;
    justify-content: center;
  }
`;

const Svg: React.FC<Props> = (props) => {
  return (
    <Wrapper className={`${props.class ?? ''}`}>
      <ReactSVG src={require(`assets/images/${props.path ? props.path : 'svg'}/${props.name}.svg`).default} />
    </Wrapper>
  )
}

export default memo(Svg)