import React, { memo, useEffect, useState } from "react";
import { Layout, Menu, MenuProps } from 'antd';
import { useHookSider } from "state/sider";
import { Line, Logo, Logout, User, Wrapper } from "./styled";
import { AppRoute, useRouteTranslation } from "config/router/config.router";
import { useHistory, useLocation } from "react-router-dom";
import SVG from 'components/SVG'
import { useAuthentication } from "hooks/useAuthentication";
import { useHookStateUser } from "state/user";
import { cutText } from "utils/string";
import { useHookStateWallet } from "state/wallet";

const { Sider } = Layout;

const SiderCpn = () => {

    const { logout, getInfo } = useAuthentication()
    const [state, actions] = useHookSider();
    const [stateWallet] = useHookStateWallet()
    const [stateUser] = useHookStateUser()
    const history = useHistory()
    const { pathname } = useLocation()
    const [keySelected, setKeySelected] = useState<string>('');

    const handleSelectedKey = () => {
        let routeExists = Object.keys(AppRoute).find((item) => AppRoute[item] === pathname)

        if (routeExists) {
            setKeySelected(AppRoute[routeExists]);
        }
    }

    const onClick: MenuProps['onClick'] = (e) => {
        history.push(e.key)
    };

    const onLogout = async () => {
        await logout().then(() => history.push(AppRoute.login))
    }

    const getInfoUser = async () => {
        await getInfo()
    }

    useEffect(() => {
        handleSelectedKey()

        // eslint-disable-next-line
    }, [pathname])

    const [width, setWidth] = useState(window.innerWidth)

    useEffect(() => {
        window.addEventListener('resize', function (event: any) {
            if (window.innerWidth <= 767) {
                actions.setCollapse(true)
                actions.setCanCollapse(false)
            } else {
                actions.setCanCollapse(true)
            }
            setWidth(window.innerWidth)
        }, true);
        // eslint-disable-next-line
    }, [width])

    useEffect(() => {
        if (window.innerWidth <= 767) {
            actions.setCollapse(true)
            actions.setCanCollapse(false)
        } else {
            actions.setCanCollapse(true)
        }
        setWidth(window.innerWidth)
        // eslint-disable-next-line
    }, [])

    useEffect(() => {
        getInfoUser()
        // eslint-disable-next-line
    }, [])

    return (
        <>
            <Wrapper>
                <Sider
                    // collapsible
                    collapsed={state?.collapse}
                    className="sider"
                    onCollapse={(value) => {
                        actions.setCollapse(value)
                    }}
                >

                    {
                        state?.collapse
                            ? <Logo className="icon">
                                <SVG path="icons" name='logo' class="logo" />
                            </Logo>
                            : <Logo className="full">
                                <SVG path="icons" name='logo-text' class="logo" />
                            </Logo>
                    }
                    <Line />

                    {
                        stateWallet.address && stateWallet.chainId && stateUser?.data?.active
                            ? (
                                <>
                                    <User style={{ justifyContent: state?.collapse ? 'center' : 'flex-start' }}>
                                        <img className="avatar" src={stateUser?.data?.avatar} alt="" />
                                        {
                                            state?.collapse
                                                ? <></>
                                                : <div>
                                                    <p className="welcome">Welcome!</p>
                                                    <p className="mail">{stateUser.data?.email.length > 16 ? cutText(stateUser?.data?.email, 16) : stateUser.data?.email.length}</p>
                                                </div>
                                        }
                                    </User>
                                </>
                            ) : <></>
                    }

                    <Menu
                        onClick={onClick}
                        className="menu no-sb"
                        mode="inline"
                        defaultOpenKeys={[
                            pathname.slice(
                                pathname.indexOf('/') + 1,
                                pathname.lastIndexOf('/') === 0
                                    ? pathname.length
                                    : pathname.indexOf('/', pathname.indexOf('/') + 1)
                            )
                        ]}
                        selectedKeys={[keySelected]}
                        items={useRouteTranslation({ show: stateWallet.address && stateWallet.chainId && stateUser?.data?.active ? true : false })}
                    />

                    <Logout onClick={onLogout}>
                        {
                            state?.collapse
                                ? <></>
                                : <p style={{ cursor: 'pointer' }}>Log out</p>
                        }
                        <SVG name="logout" />
                    </Logout>
                </Sider>
            </Wrapper>
        </>
    )
}

export default memo(SiderCpn)