import { memo, useState } from "react"
import styles from './styles.module.css'
import Table from "./components/table"
import { ethers } from "ethers";
import Svg from "components/SVG"
import { useMission } from "hooks/useMission"
import { useHookStateWallet } from "state/wallet"
import { handleNumberBC } from "utils"
import { useAuthentication } from "hooks/useAuthentication";
import { useHookLoadingConfirm } from "components/LoadingConfirm/state";
import { useHookStateMission } from "state/mission";

const Mission = () => {

  const [show, setShow] = useState(false)
  const [amount, setAmount] = useState(0)
  const { sendBUSD } = useMission()
  const [stateMission] = useHookStateMission()
  const [stateWallet] = useHookStateWallet()
  const { getInfo } = useAuthentication()
  const [stateConfirm, actionsConfirm] = useHookLoadingConfirm()

  const onSendBUSD = async () => {
    if (!stateConfirm.show) {
      if (stateWallet?.chainId && stateWallet?.address) {
        const number = await handleNumberBC(Number(amount))
        const value = ethers.BigNumber.from(number)
        actionsConfirm.setShow(true)
        actionsConfirm.setTitle('Mission Transfer BUSD')
        await sendBUSD(stateWallet?.chainId, stateWallet?.address, value, stateMission?.data?.done ?? 0).then(async () => {
          await getInfo()
          setShow(false)
          actionsConfirm.setShow(false)
        })
          .catch(() => {
            actionsConfirm.setShow(false)
          })
      }
    }
  }

  return (
    <>
      {
        show
          ? (
            <div className={styles.wrapperPopup}>
              <div className={styles.popup}>
                <div className={styles.close}
                  onClick={() => setShow(false)}
                >
                  <Svg
                    name="close"
                  />
                </div>
                <div className={styles.container}>
                  <div className={styles.content}>
                    <Svg name="dollar-fly" />
                    <p className={styles.amount}>${amount} BUSD</p>
                    <p className={styles.description}>Complete quests to get rewards</p>
                    {stateWallet?.balanceBUSD < amount
                      ? (
                        <button className={styles.submit}>
                          Insufficient balance
                        </button>
                      ) : (
                        <button className={styles.submit} onClick={onSendBUSD}>
                          Confirm
                        </button>
                      )
                    }
                  </div>
                </div>
              </div>
            </div>
          ) : (
            <div style={{ position: 'relative' }} id="mission" className="mission-page">
              <p className={`font-600 text-14 ${styles.title}`}>Mission List</p>
              <Table setAmount={setAmount} setShow={setShow} />
            </div>
          )
      }
    </>
  )
}

export default memo(Mission)