import React, { memo, useEffect, useState } from "react"
import { useHookLoadingConfirm } from "./state"
import { BackDrop, Body, Content, ContInfo, DetailInfo, Header, IconInfo, Infos, Popup, Popups, TextDetail, Wrapper } from "./styled"
import SVG from "components/SVG"
import { useLocation } from "react-router-dom"

const LoadingConfirm = () => {

    const [state] = useHookLoadingConfirm()
    const [show, setShow] = useState(false)
    const location = useLocation()

    useEffect(() => {
        setTimeout(() => {
            setShow(state?.show)
        }, 500)
    }, [state.show])

    useEffect(() => {
        if (state?.show) {
            window.onbeforeunload = function (event) {
                event.preventDefault();
                return window.confirm("Confirm refresh");
            };
        } else {
            window.onbeforeunload = null
        }
    }, [state?.show, location])

    return (
        <>
            {
                state.show
                    ? (
                        <Wrapper>
                            <BackDrop />

                            <Popups className={show ? 'active' : ''}>
                                <Popup>
                                    <Header>
                                        {
                                            state?.title
                                        }
                                    </Header>
                                    <Body>
                                        <Content>
                                            <p className="font-bold text-20">Please wait...</p>
                                        </Content>
                                        <Content>
                                            <TextDetail
                                                style={{
                                                    fontSize: '12px',
                                                    whiteSpace: 'nowrap',
                                                    alignItems: 'center',
                                                    justifyContent: 'center',
                                                    display: 'flex'
                                                }}
                                            >
                                                Please do not <span style={{ fontSize: '12px', margin: 0 }} className="danger">&nbsp;reload&nbsp;</span> or <span style={{ fontSize: '12px', margin: 0 }} className="danger">&nbsp;exit&nbsp;</span> this site during the transaction
                                            </TextDetail>
                                            <Infos>
                                                <ContInfo>
                                                    <IconInfo>
                                                        <SVG name="info" />
                                                    </IconInfo>
                                                    <DetailInfo>
                                                        <TextDetail>
                                                            Please do not <span style={{ fontSize: '12px', margin: 0 }} className="danger">reload</span> or <span style={{ fontSize: '12px', margin: 0 }} className="danger">exit</span> this site during the transaction
                                                        </TextDetail>
                                                        <TextDetail>
                                                            If during the transaction you <span style={{ fontSize: '12px', margin: 0 }} className="danger">leave</span> the website or <span style={{ fontSize: '12px', margin: 0 }} className="danger">reload</span> the page and the error arises from the bad influence of the user, we will not be responsible for this problem.
                                                        </TextDetail>
                                                        <TextDetail>
                                                            Thank you
                                                        </TextDetail>
                                                    </DetailInfo>
                                                </ContInfo>
                                            </Infos>
                                        </Content>
                                    </Body>
                                </Popup>
                            </Popups>
                        </Wrapper>
                    ) : (
                        <></>
                    )
            }
        </>
    )
}

export default memo(LoadingConfirm)