import { createStore, createHook, createContainer, createSubscriber } from 'react-sweet-state'

const Transaction = createStore({
  initialState: {
    data: [],
    dataList: [],
    currentPage: 1,
    totalPage: 0,
  },
  actions: {
    setData: (data) => ({ setState }) => {
      setState({
        data: [...data]
      })
    },
    setCurrentPage: (currentPage) => ({ setState }) => {
      setState({
        currentPage: currentPage
      })
    },
    setTotalPage: (totalPage) => ({ setState }) => {
      setState({
        totalPage: totalPage
      })
    },
    setDataList: (dataList) => ({ setState }) => {
      setState({
        dataList: [...dataList]
      })
    },
  },
  name: 'Transaction',
})

export const useHookTransaction = createHook(Transaction)
export const Container = createContainer(Transaction, {
  onInit: () => ({ setState }, props) => {
    setState({ ...props })
  },
})

export const Subscriber = createSubscriber(Transaction)
