import React, { useMemo } from 'react'
import {
  DefaultTheme,
  ThemeProvider as StyledComponentsThemeProvider,
} from 'styled-components/macro'

import { Colors } from './styled'
import { color } from './color'

export const MEDIA_WIDTHS = {
  upToExtraSmall: 500,
  upToSmall: 720,
  upToMedium: 960,
  upToLarge: 1280,
}

export enum Z_INDEX {
  deprecated_zero = 0,
  deprecated_content = 1,
  dropdown = 1000,
  sticky = 1020,
  fixed = 1030,
  modalBackdrop = 1040,
  offcanvas = 1050,
  modal = 1060,
  popover = 1070,
  tooltip = 1080,
}

function colors(): Colors {
  return {
    // text
    text1: color.Black,
    text2: color.White,
    text3: color.Transparent,
    text4: color.Transparent,
    text5: color.Transparent,
    text6: color.Transparent,
    text7: color.Transparent,
    text8: color.Transparent,
    text9: color.Transparent,
    text10: color.Transparent,

    // background
    background1: color.Black,
    background2: color.White,
    background3: color.Transparent,
    background4: color.Transparent,
    background5: color.Transparent,
    background6: color.Transparent,
    background7: color.Transparent,
    background8: color.Transparent,
    background9: color.Transparent,
    background10: color.Transparent,

    // border button
    borderButton1: color.Black,
    borderButton2: color.White,
    borderButton3: color.Transparent,
    borderButton4: color.Transparent,
    borderButton5: color.Transparent,
    borderButton6: color.Transparent,
    borderButton7: color.Transparent,
    borderButton8: color.Transparent,
    borderButton9: color.Transparent,
    borderButton10: color.Transparent,

    // button background
    button1: color.Black,
    button2: color.White,
    button3: color.Transparent,
    button4: color.Transparent,
    button5: color.Transparent,
    button6: color.Transparent,
    button7: color.Transparent,
    button8: color.Transparent,
    button9: color.Transparent,
    button10: color.Transparent,

    // border
    border1: color.Black,
    border2: color.White,
    border3: color.Transparent,
    border4: color.Transparent,
    border5: color.Transparent,
    border6: color.Transparent,
    border7: color.Transparent,
    border8: color.Transparent,
    border9: color.Transparent,
    border10: color.Transparent,
  }
}

function theme(): DefaultTheme {
  return {
    ...colors()
  }
}

export default function ThemeProvider({ children }: { children: React.ReactNode }) {
  const themeObject = useMemo(() => theme(), [])

  return <StyledComponentsThemeProvider theme={themeObject}>{children}</StyledComponentsThemeProvider>
}
