import { headersAuthToken } from 'utils/auth';
import { instance } from './../../apiBase/instance';

export const missionApi = {

	async getMission() {
		return instance.get(`mission`, await headersAuthToken());
	},

	async getAddress() {
		return instance.get(`mission/address`, await headersAuthToken());
	},

	async updateMission(data) {
		return instance.post(`mission`, data, await headersAuthToken());
	}

};

