import React, { useEffect, useState } from "react";
import { Route, Switch, Redirect } from "react-router-dom";
import { AppRoute } from "config/router/config.router";
import { Layout } from 'antd';
import { Wrapper } from './styled';

import Login from "views/Authen/Login";
import Register from "views/Authen/Register";
import Overview from 'views/Overview'

import { useHookSider } from "state/sider";
import Sider from "components/Sider";
import Header from "components/Header";
import Mission from "views/Mission";
import Comunity from "views/Comunity";
import Transaction from "views/Transaction";
import Setting from "views/Setting";
import ForgotPassword from "views/Authen/ForgotPassword";
import PrivateRoute from "./private-route";
import Verify from "views/Authen/Verify";
import RouteLogin from "./route-login";
import ActiveWallet from "components/ActiveWallet"
import WrongWallet from "components/WrongWallet"
import { useHookStateWallet } from "state/wallet";
import ActiveAccount from "components/Popup/ActivePlaftform"
import { useHookStateUser } from "state/user";
import { compareAddress } from "utils";
import ResetPassword from "views/Authen/ResetPassword";
import LoadingConfirm from 'components/LoadingConfirm'

const { Content } = Layout;

const Routers = () => {

  const [state] = useHookSider();
  const [stateWallet] = useHookStateWallet()
  const [stateUser] = useHookStateUser()

  const [activeWallet, setActiveWallet] = useState(false)
  const [wrongWallet, setWrongWallet] = useState(false)
  const [activePlaftform, setActivePlaftform] = useState(true)

  // Check active wallet
  useEffect(() => {
    if (stateWallet.address && stateWallet.chainId && !stateUser?.data?.address) {
      setActiveWallet(false)
    } else {
      setActiveWallet(true)
    }
  }, [stateWallet, stateUser])

  // Check wrong wallet
  useEffect(() => {
    if (stateWallet.address && stateWallet.chainId && stateUser?.data?.address && !stateWallet.wrongNetwork && compareAddress(stateUser?.data?.address, stateWallet?.address)) {
      setWrongWallet(false)
    } else {
      setWrongWallet(true)
    }
  }, [stateWallet, stateUser])



  // Check active plaftform
  useEffect(() => {
    if (stateWallet.address && stateWallet.chainId && stateUser?.data?.address && compareAddress(stateUser?.data?.address, stateWallet?.address) && stateUser?.data?.active) {
      setActivePlaftform(true)
    } else {
      setActivePlaftform(false)
    }
  }, [stateWallet, stateUser])

  return (
    <>
      <Switch>
        <RouteLogin exact path={AppRoute.login} component={Login} />
        <RouteLogin exact path={AppRoute.register} component={Register} />
        <RouteLogin exact path={AppRoute.code} component={Register} />
        <RouteLogin exact path={AppRoute.verify} component={Verify} />
        <RouteLogin exact path={AppRoute.forgotpassword} component={ForgotPassword} />
        <RouteLogin exact path={AppRoute.resetpassword} component={ResetPassword} />
        <PrivateRoute>
          <Wrapper collapse={state?.collapse}>
            <Layout>
              {/* SIDEBAR LEFT */}
              <Sider />

              <Layout className="site-layout">
                {/* HEADER */}
                <Header />

                {
                  stateWallet.address && stateWallet.chainId && stateUser?.data
                    ? (
                      !activeWallet
                        ? <Content className='content'><ActiveWallet /></Content>
                        : <>
                          {
                            wrongWallet
                              ? (
                                <Content className='content'>
                                  <WrongWallet />
                                </Content>
                              )
                              : (
                                <>
                                  {
                                    !activePlaftform
                                      ? (
                                        <Content className='content'>
                                          <ActiveAccount />
                                        </Content>
                                      )
                                      : (<>
                                        <Content className='content'>
                                          <Switch>
                                            <Route exact path={AppRoute.overview} component={Overview} />
                                            <Route exact path={AppRoute.mission} component={Mission} />
                                            <Route exact path={AppRoute.comunity} component={Comunity} />
                                            <Route exact path={AppRoute.transaction} component={Transaction} />
                                            <Route exact path={AppRoute.setting} component={Setting} />
                                            <Route exact path={AppRoute.all}>
                                              <Redirect to={AppRoute.overview} />
                                            </Route>
                                          </Switch>
                                        </Content>
                                      </>)
                                  }
                                </>
                              )
                          }
                        </>
                    ) : (
                      <></>
                    )
                }
              </Layout>
            </Layout>
          </Wrapper>
        </PrivateRoute>
      </Switch>
      <LoadingConfirm />
    </>
  );
};

export default Routers;
