
import { createStore, createHook, createContainer, createSubscriber } from 'react-sweet-state'

const StateUser = createStore({
  initialState: {
    data: null,
    balance: 0
  },
  actions: {
    setData: (data) => ({ setState }) => {
      setState({
        data: data
      })
    },
    setBalance: (balance) => ({ setState }) => {
      setState({
        balance: balance
      })
    },
    clear: () => ({ setState }) => {
      setState({
        data: null,
      })
    }
  },
  name: 'StateUser',
})

export const useHookStateUser = createHook(StateUser)
export const Container = createContainer(StateUser, {
  onInit: () => ({ setState }, props) => {
    setState({ ...props })
  },
})

export const Subscriber = createSubscriber(StateUser)
