import React, { memo, useEffect, useState } from "react";
import { View, WrapForm } from "./styled";
import { Checkbox, Form, Input } from 'antd';
import AuthenNavigation from 'components/Navigration/authen'
import styles from '../styles.module.css'
import { NavLink, useHistory, useParams } from "react-router-dom";
import { formatNumbersLess10 } from "utils/number";
import { useAuthentication } from "hooks/useAuthentication";
import { AppRoute } from "config/router/config.router";

const Register = () => {
    const { register } = useAuthentication()
    const [formCreate] = Form.useForm();
    const [timeLeft, setTimeLeft] = useState(0)
    const [step, setStep] = useState(0);
    const [data, setData] = useState(null)
    const params: any = useParams()
    const history = useHistory()

    const onFinishCreate = async () => {
        const fileds = formCreate.getFieldsValue()
        setData({ ...fileds })
        await register(fileds).then((res) => {
            if (res.success) {
                setStep(1)
                setTimeLeft(60)
            }
        })
    };

    const resend = async () => {
        await register(data).then((res) => {
            if (res.success) {
                setStep(1)
                setTimeLeft(60)
            }
        })
    }

    useEffect(() => {
        if (timeLeft > 0 && step === 1) {
            setTimeout(() => {
                setTimeLeft(timeLeft - 1)
            }, 1000)
        }
        // eslint-disable-next-line
    }, [timeLeft])

    useEffect(() => {
        if (params?.id) {
            formCreate.setFieldsValue({
                refBy: params.id
            });
            localStorage.setItem('ref_code', params.id);
            history.push(AppRoute.register)
        } else {
            const idCode = localStorage.getItem('ref_code');
            formCreate.setFieldsValue({
                refBy: idCode
            });
        }
        // eslint-disable-next-line
    }, [params, history])

    return (
        <>
            <View>
                <AuthenNavigation />
                <WrapForm>
                    {
                        step === 0
                            ? (
                                <Form
                                    name="basic"
                                    form={formCreate}
                                    className={`${styles.smt_form}`}
                                    onFinish={onFinishCreate}
                                    autoComplete="off"
                                >
                                    <p className={styles.smt_titleForm}>Create Your Account</p>
                                    <p className={styles.smt_descriptionForm}>Welcome! Please enter your details.</p>
                                    <Form.Item
                                        label="Email"
                                        name="email"
                                        rules={[{ required: true, message: 'Please input your email!' }]}
                                    >
                                        <Input type="email" placeholder="example @mail.com" />
                                    </Form.Item>

                                    <Form.Item
                                        label="Password"
                                        name="password"
                                        rules={[{ required: true, message: 'Please input your password!' }]}
                                    >
                                        <Input.Password placeholder="min 8 characters" />
                                    </Form.Item>


                                    <Form.Item
                                        label="Referral code*"
                                        name="refBy"
                                        rules={[{ required: true, message: 'Please input your referral code!' }]}
                                    >
                                        <Input />
                                    </Form.Item>

                                    <Form.Item className="remember-me">
                                        <Form.Item className="_remember-me" name="remember" valuePropName="checked" rules={[{ required: true, message: 'Please accept Terms of Service and Privacy Policy' }]}>
                                            <Checkbox>
                                                <span
                                                    style={{
                                                        whiteSpace: 'pre-wrap',
                                                        fontWeight: '500',
                                                        fontSize: '14px',
                                                        lineHeight: '17px',
                                                        color: 'rgba(255,255,255,0.8)'
                                                    }}
                                                >
                                                    I have read and agree to S M's <NavLink
                                                        style={{
                                                            whiteSpace: 'pre-wrap',
                                                            fontWeight: '500',
                                                            fontSize: '14px',
                                                            lineHeight: '17px',
                                                            color: 'rgba(255,255,255,0.8)',
                                                            textDecoration: 'underline'
                                                        }}
                                                        to="/">Terms of Service and Privacy Policy.</NavLink>
                                                </span>
                                            </Checkbox>
                                        </Form.Item>
                                    </Form.Item>

                                    <Form.Item>
                                        <button className={styles.smt_buttonSubmit} type="submit">
                                            Create Account
                                        </button>
                                    </Form.Item>
                                    <Form.Item style={{ marginBottom: 0 }}>
                                        <div className={styles.smt_moreOption}>
                                            <p>Already have an account? &nbsp;</p>
                                            <NavLink className={styles.smt_moreOption_link} to="/login">Login</NavLink>
                                        </div>
                                    </Form.Item>
                                </Form>
                            ) : (
                                <Form
                                    name="basic"
                                    className={`${styles.smt_form}`}
                                    autoComplete="off"
                                >
                                    <p className={styles.smt_titleForm}>Email Verification</p>
                                    <p style={{ marginBottom: 0 }} className={styles.smt_descriptionForm}>Please check your email box. Confirmation link is valid for 5 minutes.</p>
                                    <p className={styles.smt_descriptionForm}>Please check your spam folder if you do not receive the email.</p>
                                    <div className={styles.resend}>
                                        <Form.Item style={{ marginBottom: 0 }}>
                                            <div className={styles.smt_moreOption}>
                                                <p>Didn't receive the code? &nbsp;</p>
                                                {
                                                    timeLeft > 0
                                                        ? <p className={styles.smt_resendCode}>Resend in {formatNumbersLess10(timeLeft)} seconds</p>
                                                        : <p onClick={resend} className={styles.smt_resendCode}>Resend</p>
                                                }
                                            </div>
                                        </Form.Item>
                                    </div>
                                </Form>
                            )
                    }
                </WrapForm>
            </View >
        </>
    )
}

export default memo(Register)