import { headersAuthToken } from 'utils/auth';
import { instance } from './../../apiBase/instance';

export const authApi = {

	login(data) {
		return instance.post(`user/login`, data);
	},

	register(data) {
		return instance.post(`user/register`, data);
	},

	verify(query) {
		return instance.get(`user/verify${query}`);
	},

	async validateToken() {
		return instance.get(`user/token/validate`, await headersAuthToken());
	},

	async logout() {
		return instance.get(`user/logout`, await headersAuthToken());
	},

	async changePassword(data) {
		return instance.put(`user/change-password`, data, await headersAuthToken());
	},

	async getInfo() {
		return instance.get(`user/me`, await headersAuthToken())
	},

	async sendResetPassword(data) {
		return instance.post(`user/password`, data, await headersAuthToken())
	},

	async checkLinkReset(query) {
		return instance.get(`user/password${query}`, await headersAuthToken())
	},

	async resetPassword(query, data) {
		return instance.put(`user/password${query}`, data, await headersAuthToken())
	}

};

