import type { MenuProps } from 'antd';
import SVG from 'components/SVG'

type MenuItem = Required<MenuProps>['items'][number];

function getItem(
  label: React.ReactNode,
  key: React.Key,
  icon?: React.ReactNode,
  children?: MenuItem[],
): MenuItem {
  return {
    key,
    icon,
    children,
    label,
  } as MenuItem;
}

export const useRouteTranslation = ({ show }) => {
  if (show) {
    const items: MenuItem[] = [
      getItem('Overview', AppRoute.overview, <SVG name='overview' />),
      getItem('Mission', AppRoute.mission, <SVG name='mission' />),
      getItem('Community', AppRoute.comunity, <SVG name='comunity' />),
      getItem('Transaction', AppRoute.transaction, <SVG name='transaction' />),
      getItem('Setting', AppRoute.setting, <SVG name='setting' />),
    ];

    return items
  }
  return []
}

export const AppRoute = {
  all: '*',
  basic: '/',
  login: '/login',
  verify: '/verify',
  register: '/register',
  overview: '/overview',
  mission: '/mission',
  comunity: '/comunity',
  transaction: '/transaction',
  setting: '/setting',
  forgotpassword: '/forgot-password',
  resetpassword: '/reset-password',
  code: '/code/:id',
}