import { getAddress } from '@ethersproject/address'
import { BigNumber } from "@ethersproject/bignumber";
import async from 'async'
import { secretStringWallet } from '../constants'
const Cryptr = require('cryptr');

export function isAddress(value: any): string | false {
    try {
        return getAddress(value)
    } catch {
        return false
    }
}

export function calculateGasMargin(value: BigNumber): BigNumber {
    return value
        .mul(BigNumber.from(10000).add(BigNumber.from(1000)))
        .div(BigNumber.from(10000));
}

export function shortenAddress(address: string, chars = 5): string {
    if (!address) {
        return ''
    }
    const parsed = isAddress(address)
    if (!parsed) {
        throw Error(`Invalid 'address' parameter '${address}'.`)
    }
    return `${parsed.substring(0, chars)}...${parsed.substring(42 - chars)}`
}

export function capitalizeFirstLetter(string) {
    return string.charAt(0).toUpperCase() + string.slice(1);
}

export const handleParams = (queries) => {
    let query = ''
    async.map(Object.keys(queries), (item) => {
        if (queries[item] !== null) {
            if (query === '') {
                query += `?${item}=${queries[item]}`
            } else {
                query += `&${item}=${queries[item]}`
            }
        }
    })

    return query
}

export const handleNumberBC = async (value) => {
    const num = (value * 1e18).toString()

    if (num.indexOf('e+') === -1) {
        return num
    }
    return await toFixed(num)

}

function toFixed(x) {
    if (Math.abs(x) < 1.0) {
        var e = parseInt(x.toString().split('e-')[1]);
        if (e) {
            x *= Math.pow(10, e - 1);
            x = '0.' + (new Array(e)).join('0') + x.toString().substring(2);
        }
    } else {
        // eslint-disable-next-line
        var e = parseInt(x.toString().split('+')[1]);

        if (e > 20) {
            e -= 20;
            x /= Math.pow(10, e);
            x += (new Array(e + 1)).join('0');
        }
    }
    return x;
}

export const decodecryptr = (data: string) => {
    const cryptr = new Cryptr(secretStringWallet.key);
    return cryptr.decrypt(data)
}

export const inoutCheckTransaction = (note) => {
    if (note === 'active' || note === 'sent') return false
    else return true
}

export const compareAddress = (add1: string, add2: string) => {
    if (add1.toLowerCase() === add2.toLowerCase()) return true
    else return false
}