import React, { memo } from "react";
import { View, WrapForm } from "./styled";
import { Checkbox, Form, Input } from 'antd';
import { useAuthentication } from "hooks/useAuthentication";
import AuthenNavigation from 'components/Navigration/authen'
import styles from '../styles.module.css'
import { NavLink, useHistory } from "react-router-dom";
import { AppRoute } from "config/router/config.router";

const Login = () => {
    const [form] = Form.useForm();
    const history = useHistory()
    const { login } = useAuthentication()

    const onFinish = async () => {
        const fileds = form.getFieldsValue()
        await login(fileds).then((res) => {
            if (res.success) {
                history.push(AppRoute.overview)
            }
        })
    };

    return (
        <>
            <View>
                <AuthenNavigation />
                <WrapForm>
                    <Form
                        name="basic"
                        form={form}
                        className={`${styles.smt_form}`}
                        labelCol={{ span: 8 }}
                        wrapperCol={{ span: 16 }}
                        onFinish={onFinish}
                        autoComplete="off"
                    >
                        <p className={styles.smt_titleForm}>Login</p>
                        <p className={styles.smt_descriptionForm}>Please enter your details.</p>
                        <Form.Item
                            label="Email"
                            name="email"
                            rules={[{ required: true, message: 'Please input your email!' }]}
                        >
                            <Input type="email" placeholder="example @mail.com" />
                        </Form.Item>

                        <Form.Item
                            label="Password"
                            name="password"
                            rules={[{ required: true, message: 'Please input your password!' }]}
                        >
                            <Input.Password placeholder="min 8 characters" />
                        </Form.Item>

                        <Form.Item className="remember-me">
                            <Form.Item className="_remember-me" name="remember" valuePropName="checked">
                                <Checkbox>Remember me in 30 days</Checkbox>
                            </Form.Item>
                            <NavLink className="forgot-password" to={AppRoute.forgotpassword}>
                                Forgot password
                            </NavLink>
                        </Form.Item>

                        <Form.Item>
                            <button className={styles.smt_buttonSubmit} type="submit">
                                Login
                            </button>
                        </Form.Item>
                        <Form.Item style={{ marginBottom: 0 }}>
                            <div className={styles.smt_moreOption}>
                                <p>Dont have an account?&nbsp;</p>
                                <NavLink className={styles.smt_moreOption_link} to="/register">Sign up for free</NavLink>
                            </div>
                        </Form.Item>
                    </Form>
                </WrapForm>
            </View>
        </>
    )
}

export default memo(Login)